#content {
  min-height: calc(100% - 10rem);
  padding: 10px 0;
}
#content #box {
  width: 1000px;
  margin: 20px auto;
  padding: 30px 40px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 8px;
  box-shadow: 0 4px 8px 6px rgba(7, 17, 27, 0.06);
  overflow: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  -webkit-animation: up 1s;
  animation: up 1s;
}
#content #box:hover {
  box-shadow: 0 4px 12px 12px rgba(7, 17, 27, 0.15);
}
#content #box audio {
  display: none;
}
#content #box #text span {
  font-size: 25px;
  font-family: Roboto Mono;
  line-height: 1.5;
  display: inline-block;
}
#content #box #input {
  margin: 25px 0;
}
#content #box #input .ant-input {
  width: 780px;
  font-size: 18px;
  font-weight: 500;
  float: left;
}
#content #box #input .ant-btn {
  width: 120px;
  font-size: 18px;
  float: right;
}
#content #box #keyboard {
  width: 790px;
  margin: 30px 0;
  float: left;
}
#content #box #keyboard .key {
  width: 48px;
  height: 48px;
  margin: 2.5px;
  font-size: 13px;
  text-align: center;
  border: 1px solid #aaa;
  border-radius: 7px;
  float: left;
  -webkit-transition: 0.15s;
  transition: 0.15s;
}
#content #box #keyboard .key p {
  margin: 2.5px 0;
}
#content #box #keyboard .key i {
  line-height: 50px;
}
#content #box #keyboard .left {
  text-align: left;
  padding-left: 8px;
  line-height: 42.5px;
}
#content #box #keyboard .center {
  line-height: 42.5px;
}
#content #box #keyboard .right {
  text-align: right;
  padding-right: 8px;
  line-height: 42.5px;
}
#content #box #data {
  margin-left: 35px;
  float: left;
}
#content #box #data .title {
  font-size: 15px;
  margin: 35px 0 8px;
}
#content #box #data .detail {
  font-size: 30px;
  margin: -10px 0;
}
#content #box #data .detail span {
  font-size: 10px;
  margin-left: 10px;
}
