#header {
  height: 4rem;
  padding: 0.5rem 0;
  box-shadow: 0 0 12px 0 rgba(7, 17, 27, 0.06);
  -webkit-animation: down 1s;
  animation: down 1s;
}
#header #nav {
  margin: 0 auto;
  max-width: 1000px;
}
#header #nav #logo {
  height: 3rem;
  padding: 0.4rem 0.5rem 0.6rem 0;
  float: left;
}
#header #nav #title {
  font-size: 20px;
  font-weight: bold;
  font-family: Trebuchet MS;
  line-height: 3rem;
  float: left;
}
#header #nav #btn {
  padding: calc((3rem - 40px) / 2) 0;
  float: right;
}
